.Wallet {
  position: relative;
  border: 1px solid black;
  border-radius: 2px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  grid-row: 4 / 6;
  grid-column: 2 / 3;
}

.Wallet h2 {
  font-size: 1.8rem;
  position: absolute;
  top: 0;
  left: 2%;
  transform: translateY(-55%);
  background-color: #fff;
  padding: 2px;
}

.Wallet h3 {
  font-size: 6rem;
  margin-bottom: -2.4rem;
}

.Wallet div {
  display: flex;
  padding: 1rem;
  font-size: 2rem;
}
